import express from 'express';
import loansController from '../controller/loancontroller';
import repaymentsController from '../controller/repaymentcontroller';
import userController from '../controller/userscontroller';
import authentication from '../helpers/authentication';

const router = express.Router();
// post routers

router.get('/api/v1/users',authentication.adminTokenRequired,userController.getuser);
router.get('/api/v1/loans',loansController.getloans);
router.get('/api/v1/repayments', repaymentsController.getrepayments);
router.get('/api/v1/loans/notrepaid',authentication.adminTokenRequired,loansController.getnotrepaid);
router.get('/api/v1/loans/repaid',authentication.adminTokenRequired,loansController.getrepaid);


// post routers
router.post('/api/v1/loans', loansController.createloans);
router.post('/api/v1/loans/:loanid/repayments', repaymentsController.createrepayments);
router.post('/api/v1/auth/signup', userController.createUser);
router.post('/api/v1/auth/signin', userController.login);


// get by id  routers
router.get('/api/v1/users/:id',authentication.adminTokenRequired,userController.getOneuser);
router.get('/api/v1/loans/:id',authentication.adminTokenRequired,loansController.getOneloan);
router.get('/api/v1/loans/:id/repayments',repaymentsController.getOnepayment);

// patch router
router.patch('/api/v1/loans/:id/',loansController.verifyloan);
router.patch('/api/v1/users/:email/verify',authentication.adminTokenRequired, userController.verifyuser);


// delete routers
router.delete('/api/v1/loans/:id',authentication.adminTokenRequired,loansController.deleteloan);


export default router;
